import { Model, model } from '@engine/core';

@model()
export class ParameterDatetimeModel extends Model {
    public static xtype = 'report.model.main.parameter_container.controller.datetime';

    private options: {
        value: any
    };

    public state() {
        return {
            value: this.options.value,
            valid: true,
            operationCount: 0,
        };
    }

    public computed = {
        validOperationCount: () => this.model.operationCount > 0,
    }

    public actions = {
        setValue: (v: any) => {
            this.model.value = v;
        },

        setValid: (v: boolean) => {
            this.model.valid = !!v;
        },

        operationCountUp: () => {
            this.model.operationCount++;
        },
    }
}
